<template>
  <div class="l-topnav d-none d-md-flex justify-content-center pr-md-5 pl-md-5">
    <div class="l-topnav-container flex-grow-1">
      <button class="btn btn-link" @click="$router.push('apostas')" :class="{'active' : linkActive === 'apostas'}">Faça sua aposta</button>
      <button class="btn btn-link" @click="$router.push('concursos')" :class="{'active' : linkActive === 'concursos'}">Concursos anteriores</button>
      <button class="btn btn-link" @click="$router.push('funciona')" :class="{'active' : linkActive === 'funciona'}">Como funciona</button>
      <!-- <div v-click-outside="nameOfCustomEventToCall"> -->
    </div>
  </div>
</template>
<script>
import Vue from "vue";
export default {
  name: 'TopNav',
  data() {
    return {
      linkActive: 'aposta'
    }
  },
  created() {
    this.linkActive = this.$router.history.current.name;
  },
  watch: {
    $route(to, from) {
      this.linkActive = to.name;
    }
  }
}
</script>
