<template>
  <div class="all-balls">
    <div class="all-balls-title">
      <div class="text-1">Escolha os números da aposta</div>
      <button @click="randonmizer()" class="text-2 btn btn-link">RANDOMIZAR</button>
    </div>
    <div class="all-balls-items">
      <div
        class="all-balls-ball"
        :class="{ selected: ball.selected }"
        @click="changeBall(ball)"
        v-for="(ball, index) in allBalls"
        :key="index"
      >{{ ball.stakeValue }}</div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
export default {
  name: 'all-balls',
  props: ["size", "unselect"],
  data() {
    return {
      allBalls: [],
      ballCount: 0,
    }
  },
  methods: {
    randonmizer() {
      this.allBalls = this.createAllBalls();
      this.$emit("clear");
    },
    createAllBalls() {
      let allBalls = new Array(this.size).fill({ stakeValue: 0, selected: false });
      allBalls = allBalls.map(item => {
        let stakeValue = Math.floor(Math.random() * (+100 - +1) + +1);
        stakeValue = stakeValue < 10 ? "0" + stakeValue : "" + stakeValue;
        return {
          ...item,
          stakeValue
        }
      })
      return allBalls;
    },
    changeBall(ball) {
      if (!ball.selected) {
        this.addBall(ball);
      } else {
        this.removeBall(ball);
      }
    },
    addBall(ball) {
      if (this.ballCount < 6) {
        ball.selected = true;
        this.$emit('addball', ball);
        this.ballCount++;
      }
    },
    removeBall(ball) {
      this.deleteBall(ball);
      this.$emit('removeball', ball);
    },
    deleteBall(ball) {
      ball.selected = false;
      this.ballCount--;
    }
  },
  mounted() {
    this.allBalls = this.createAllBalls();
  },
  watch: {
    unselect(ball, oldBall) {
      if (ball.stakeValue !== oldBall.stakeValue) {
        this.deleteBall(ball);
      }
    }
  }
}
</script>

