<template>
  <div class="ball-pallete d-flex justify-content-between">
    <div class="balls d-flex align-items-center">
      <div class="ball d-flex justify-content-center" v-for="(ball, index) in balls" :key="index">
        <div
          class="ball-item d-flex justify-content-center align-items-center"
          v-if="ball.stakeValue"
          @click="removeBall(ball)"
        >{{ ball.stakeValue }}</div>
        <div
          class="ball-empty d-flex justify-content-center"
          v-if="!ball.stakeValue"
        >{{ ball.stakeValue }}</div>
      </div>
    </div>
    <div class="add">
      <img src="@/assets/img/add.svg" alt="adicionar aposta" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      balls: [],
      ballCount: 0
    }
  },
  props: ["size", "addedBall", "removedBall"],
  methods: {
    createBalls(size) {
      return new Array(size).fill({ stakeValue: 0, selected: false });
    },
    removeBall(ball) {
      const ballIndex = this.deleteBall(ball);
      if (ballIndex > -1) {
        this.$emit("removeball", ball);
      }
    },
    deleteBall(ball) {
      const ballIndex = this.findBall(ball);
      if (ballIndex > -1) {
        this.balls.splice(ballIndex, 1);
        this.balls.push({ stakeValue: 0, selected: false });
        this.ballCount--;
      }
      return ballIndex;     
    },
    findBall(ball){
      const ballIndex = this.balls
        .map(item => item.stakeValue)
        .indexOf(ball.stakeValue);
        return ballIndex;
    }
  },
  watch: {
    addedBall(ball, oldBall) {
      if (ball.stakeValue && ball.stakeValue !== oldBall.stakeValue) {
        const lastItem = this.balls[this.balls.length - 1];
        if (!lastItem.stakeValue) {
          this.balls.splice(this.ballCount++, 1, Object.assign({}, ball));
        }
        ball.stakeValue = 0;
      }
    },
    removedBall(ball, oldBall){
      if (ball.stakeValue && ball.stakeValue !== oldBall.stakeValue) {
        this.deleteBall(ball);
        ball.stakeValue = 0;
      }
    }
  },
  mounted() {
    this.balls = this.createBalls(this.size);
  }
}
</script>

