          box-shadow: 0 7px 11px -3px rgba(2, 222, 119, 0.19);
          background-image: linear-gradient(125deg, #00de77, #18bf75);
          border-radius: 50%;
          width: 23px;
          height: 23px;
          font-size: 0.625rem;
