<template>
  <div class="stakes-ball d-grow flex-column">
    <ball-pallete
      :addedBall="addedBall"
      :removedBall="removedBall"
      :size="6"
      @removeball="removePalleteBall($event)"
    ></ball-pallete>
    <all-balls
      :unselect="unselectedBall"
      :size="42"
      ref="ballArr"
      @addball="addBall($event)"
      @removeball="removeBall($event)"
      class="flex-grow-1"
    ></all-balls>
  </div>
</template>

<script>
import AllBalls from "./all-balls/all-balls.vue";
import BallPallete from "./ball-pallete/ball-pallete.vue";
export default {
  name: "StakesBall",
  components: { AllBalls, BallPallete },
  data() {
    return {
      addedBall: {},
      removedBall: {},
      unselectedBall: {}
    }
  },
  methods: {
    addBall(ball) {
      this.addedBall = Object.assign({}, ball);;
    },
    removeBall(ball) {
      this.removedBall = Object.assign({}, ball);;
    },
    removePalleteBall(ball) {
      this.unselectedBall = Object.assign({}, ball);;
    }
  },
}
</script>

