<template>
  <div id="app">
    <l-toolbar v-if="showToolbar"></l-toolbar>
    <l-topnav v-if="showTopNav"></l-topnav>
    <div class="main justify-content-center pr-md-5 pl-md-5">
      <router-view />
    </div>
    <l-footer />
  </div>
</template>
<script>
import Vue from "vue"
import LToolbar from "@/components/l-toolbar.vue";
import LTopnav from "@/components/l-topnav.vue";
import LFooter from "@/components/l-footer.vue";
import Stakes from "@/views/stakes/stakes.vue";
export default {
  name: "App",
  components: {
    LToolbar,
    LTopnav,
    LFooter,
    Stakes,
  },
  data() {
    return {
      showToolbar: false,
      showTopNav: false,
    }
  },
  watch: {
    $route(to, from) {
      this.showToolbar = to.name !== 'home';
      this.showTopNav = to.name !== 'home';
    }
  }
};
</script>
